<template>
  <div id="home">
    <h1>Send mails <span :style="{transition: '500ms', opacity: opacity, color: 'crimson'}">{{ text }}</span>!</h1>
    <router-link to="/documentation">
      <DarkButton style="padding:12px; margin-top: 10%; font-size: 22px;" :border="true" :fill="true" value="Get Started" />
    </router-link>
  </div>
</template>

<style scoped>
#home{
position: absolute;top: 10%;left:0;
width: 100%;
height: 90%;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}
</style>

<script>
import DarkButton from 'yefeeui/components/Buttons/DarkButton'
export default {
  name:'Home',
  data(){
    return{
      texts:['easily', 'dynamical', 'with Send-Mail-API'],
      text:'easily',
      count:0,
      opacity: 1
    }
  },

  components:{
    DarkButton
  },


  created() {
  setInterval(()=>{
    if(this.count === 2){
      this.count = 0
    }

    else{
      this.count++
    }
    this.opacity = 0;
    setTimeout(()=>{
      this.opacity = 1;
      this.text  = this.texts[this.count]
    },501)

  },2500)
  }
}
</script>
